.checkout{
          width: 100%;
          padding: 20px;
}
.checkout .checkout_container{
        width: 100%;
          
}
.check_cart{
          width: 100%;
          display: flex;
          flex-direction: column;
          padding-top: 40px;
          padding-bottom: 40px;
          align-items: center;
}
.check_cart table{
       border-collapse: collapse;
       width: 80%;
}
.check_cart table thead th{
          font-size: 14pt;
}

.td_name{
          width: 10%;
          padding: 5px;
          font-size: 10pt;
}
.td_pic{
          width: 20%;
          height: auto;  
}
.td_pic img{
          width: 70%;
          height: 70%;
}
.td_price{
          width: 6%;
          font-size: 14pt;color: rgb(52, 88, 16);
}
.td_quan{
          width: 10%;
          padding: 5px;
          
}
.td_quan input{
          width: 50px;
          text-align: center;
}
.td_total{
          font-size: 14pt;
          color: rgb(52, 88, 16);
}
.td_del button{
          width: 30px;
          height: 30px;
          background-color: rgb(255, 255, 255);
          color: red;
          border-radius: 5px;
}
.check_cus{
          width: 100%;
          display: flex;
          justify-content: center;
}
.cus_info{
          width: 50%;
          display: flex;
          flex-direction: column;
          padding-right: 20px;
}
.cus_info div{
          display: flex;
          justify-content: space-between;
          line-height: 25px;
          max-width: 350px;
          padding-top: 20px;
   
}
.cus_info div input {
          width: 60%;
          height: 25px;
          padding-left: 10px;
          padding-right: 10px;
}
@media only screen and (max-width: 900px) {
          .check_cus{
                    width: 100%;
                    display: flex;
                    flex-direction: column;
          }
          .cus_info div input {

                    height: 25px;
                    padding-left: 10px;
                    padding-right: 10px;
          }
          .cus_info{
                    width: 100%;
          
          }
          .check_cart table{
                    width: 100%;
             }
             .check_cart table thead th{
                       font-size: 10pt;
             }
             
             .td_name{
                       width: 100px;
                       padding: 5px;
                       font-size: 10pt;
             }
             .td_pic{
                       width: 20%;
                       height: auto;  
             }
             .td_pic img{
                       width: 100%;
                       height: 100%;
             }
             .td_price{
                       width: 6%;
                       font-size: 10pt;color: rgb(52, 88, 16);
             }
             .td_quan{
                       width: 10%;
                       padding: 5px;
                       
             }
             .td_quan input{
                       width: 50px;
                       text-align: center;
             }
             .td_total{
                       font-size: 14pt;
                       color: rgb(52, 88, 16);
             }
             .td_del button{
                       width: 30px;
                       height: 30px;
                       background-color: rgb(255, 255, 255);
                       color: red;
                       border-radius: 5px;
             }
}